export class StorageKeys {
    constructor(config = {}) {
        this.namespace = config.namespace || '';
    }
    key(name) {
        return this.namespace ? `${this.namespace}:${name}` : name;
    }
    // Session storage keys (prefixed with session:)
    get sessionDecryptedBackup() {
        return this.key('session:decryptedBackup');
    }
    get sessionPaymentKey() {
        return this.key('session:paymentKey');
    }
    get sessionOrdinalKey() {
        return this.key('session:ordinalKey');
    }
    get sessionOAuthState() {
        return this.key('session:oauthState');
    }
    // Local storage keys (ONLY encrypted data allowed)
    get encryptedBackup() {
        return this.key('encryptedBackup');
    }
    // BAP rotation storage key (used in useBapKeyRotation)
    getBapRotationKey(idKey) {
        return this.key(`bap-rotation-${idKey}`);
    }
}
// Default configurations for different environments
export const DEFAULT_STORAGE_KEYS = new StorageKeys();
export const STORYBOOK_STORAGE_KEYS = new StorageKeys({ namespace: 'storybook' });
// Helper to create storage keys with custom namespace
export function createStorageKeys(namespace) {
    return new StorageKeys({ namespace });
}
/**
 * Usage Examples:
 *
 * // Default namespace (no prefix)
 * const keys = createStorageKeys();
 * keys.encryptedBackup // -> 'encryptedBackup'
 *
 * // Storybook namespace
 * const storybookKeys = createStorageKeys('storybook');
 * storybookKeys.encryptedBackup // -> 'storybook:encryptedBackup'
 *
 * // Development namespace
 * const devKeys = createStorageKeys('dev');
 * devKeys.sessionPaymentKey // -> 'dev:session:paymentKey'
 *
 * // Configuration in BitcoinAuthConfig:
 * const config = {
 *   storageNamespace: 'myapp-prod',
 *   // ... other config
 * };
 */
