// Combined hook for toggling likes
export function useToggleLike(options = {}) {
    const likePost = useLikePost(options);
    const unlikePostMutation = useUnlikePost(options);
    return {
        like: likePost,
        unlike: unlikePostMutation,
        toggle: (txid, emoji, currentlyLiked) => {
            if (currentlyLiked) {
                return unlikePostMutation.mutate({ txid, emoji });
            }
            return likePost.mutate({ txid, emoji });
        },
        isLoading: likePost.isLoading || unlikePostMutation.isPending,
        error: likePost.error || unlikePostMutation.error,
    };
}
