        broadcastSignatureAsync: signing.broadcastSignatureAsync,
        // Key rotation operations
        rotateKey: keyRotation.rotateKey,
        rotateKeyAsync: keyRotation.rotateKeyAsync,
        rotationHistory: keyRotation.rotationHistory,
