        isScanning: profileSync.isScanning,
        syncProgress: profileSync.progress,
        currentSyncId: profileSync.currentId,
        foundProfileCount: profileSync.foundCount,
        syncResult: profileSync.result,
        isSyncSuccess: profileSync.isSuccess,
